	module.exports = {"data":{"allMarkdownRemark":{"totalCount":6,"edges":[{"node":{"fields":{"slug":"/rip-leonard-nimoy"},"excerpt":"","timeToRead":1,"frontmatter":{"title":"RIP Leonard Nimoy","tags":["quote post","The Simpsons","Star Trek","Leonard Nimoy"],"cover":"https://unsplash.it/1152/300/?random?StarTrek","date":"2015-02-28T00:25"}}},{"node":{"fields":{"slug":"/star-wars-posters"},"excerpt":"Love these Star Wars posters  Source  Flickr","timeToRead":1,"frontmatter":{"title":"Star Wars Posters","tags":["quote post","Star Wars"],"cover":"https://unsplash.it/1152/300/?random?StarWars","date":"2015-01-16T16:59"}}},{"node":{"fields":{"slug":"/overcasts-2014-sales-numbers-marcoorg"},"excerpt":"Overcast’s 2014 sales numbers – Marco.org Very positive post from Marco Arment about the revenue generated by his latest app Overcast…","timeToRead":1,"frontmatter":{"title":"Overcast's 2014 sales numbers - Marco.org","tags":["quote post"],"cover":"https://unsplash.it/1152/300/?random?Sales","date":"2015-01-16T08:27"}}},{"node":{"fields":{"slug":"/5-ways-to-burn-out-programming"},"excerpt":"I've just read a great blog post by  Josh Braegger . I highly recommend giving it a read. 5 Ways To Burn Out Programming 1. Think about your…","timeToRead":1,"frontmatter":{"title":"5 Ways To Burn Out Programming","tags":["advice","burn out","quote post"],"cover":"https://unsplash.it/1152/300/?random?BurnOut","date":"2014-03-29T10:19"}}},{"node":{"fields":{"slug":"/design-advice"},"excerpt":"Source  goodfuckingdesignadvice.com","timeToRead":1,"frontmatter":{"title":"Good Fucking Design Advice","tags":["design","quotes","quote post"],"cover":"https://unsplash.it/1152/300/?random?Design","date":"2014-01-25T12:43"}}},{"node":{"fields":{"slug":"/we-give-a-f-how-the-site-loads"},"excerpt":"Looks like some people have been having difficulty with deviantART recently. The site has been loading, but not the CSS. What was the issue…","timeToRead":1,"frontmatter":{"title":"We Give a F*** How the Site Loads","tags":["code","quote post"],"cover":"https://unsplash.it/1152/300/?random?FuckCss","date":"2013-08-13T22:57"}}}]},"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"tag":"quote post","nodes":[{"node":{"frontmatter":{"title":"We Give a F*** How the Site Loads","tags":["code","quote post"],"cover":"https://unsplash.it/1152/300/?random?FuckCss","date":"2013-08-13T22:57","author":"eddie"},"fields":{"slug":"/we-give-a-f-how-the-site-loads"},"html":"<p>Looks like some people have been having difficulty with deviantART recently. The site has been loading, but not the CSS.</p>\n<p>What was the issue? Well here's the bug report...</p>\n<blockquote>\n<p>I just wanted to let you know that the reason why deviantART's CSS isn't loading properly for some people is because one of your CSS files has f*** in a stylesheet comment.</p>\n</blockquote>\n<p>Yes, a comment in the CSS that included the word \"fuck\" was being picked up by over overzealous filtering systems, resulting in the file being blocked, and not loaded.</p>\n<p>What was the fix? Well besides removing the comment from the file, deviantART have now moved to use <a href=\"http://lesscss.org\">LESS</a>, a CSS preprocessor.</p>\n<p>This isn't a blog post to talk about the benefits of CSS preprocessors, but as deviantART inform us they cannot stop their developers from swearing in comments, this should stop these developer comments from appearing in served css files again.</p>\n<p><small>Source <a href=\"http://dt.deviantart.com/journal/We-Give-a-F-How-the-Site-Loads-392679726\">deviantART</a></small></p>","timeToRead":1}}],"page":2,"pages":2,"total":6,"limit":5,"prev":"/tags/quote-post/"}}

